.filter {
    background: #fff;
    border: 1px solid #DFE5EC;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    text-align: left;
    height: 250px;
    width: 230px;
    margin-bottom: 20px;
}

@media (max-width: 767px) {
    .filter {
        height: 260px;
        min-width: 300px;
        margin: 0 auto 20px;
    }
}

.filter-header {
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #4A4A4A;
    padding: 20px 20px 5px;
}

.checkbox-list {
    font-size: 13px;
    font-weight: normal;
    line-height: 20px;
}
